body {
  font-family: "Open Sans", Arial, sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
.title {
  text-align: center;
  width: 360px;
}
.grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;
}
.year {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  width: 364px;
}
.week {
  border: 1px solid lightgray;
  height: 6px;
  width: 6px;
  margin: 0.5px;
}
.week.filled {
  border-color: lightblue;
  background-color: lightblue;
}
